.product-color .color1 {
  width: 24px;
  height: 24px;
  background-color: #5F3E2D;
  border-radius: 3px; }

.product-color .color2 {
  width: 24px;
  height: 24px;
  background-color: #B75929;
  border-radius: 3px; }

.pagination li {
  border: 1px solid #555555;
  transition: all 0.6s; }
  .pagination li a {
    padding: 12px 16px;
    display: block;
    color: #555555;
    font-size: 18px;
    transition: all 0.6s; }
    @media only screen and (max-width: 415px) {
      .pagination li a {
        font-size: 16px; } }
    .pagination li a:hover, .pagination li a:focus {
      color: #fff; }
  .pagination li:hover, .pagination li:focus {
    background-color: #000000; }

.details-icon, .details {
  line-height: 1.5; }

.qa-container {
  max-width: 856px; }
  @media only screen and (max-width: 768px) {
    .qa-container {
      max-width: 100%;
      padding-left: 36px;
      padding-right: 36px; } }
  @media only screen and (max-width: 415px) {
    .qa-container {
      padding: 0 12px;
      margin-left: -12px;
      margin-right: -12px; } }

.blog {
  margin-left: -12px;
  margin-right: -12px; }
  .blog .blog-details {
    display: flex;
    flex-direction: column;
    justify-content: space-between; }

.blog-inner-nav-link a {
  display: block;
  color: #000000;
  transition: all 0.6s; }
  .blog-inner-nav-link a:hover, .blog-inner-nav-link a:focus {
    color: #AA0601; }
